.navbar{
    display: flex;
    /* padding: 5px; */
    justify-content: space-between; 
    align-items: center;
   border-bottom: 1px solid black;
}
.wrapper-left{
    display: flex;
    align-items: center;
    margin-left: 4vw;
    margin-top: 0.4vw;
    margin-bottom: 0.4vw;
    align-items: center;
    overflow: hidden;
    cursor: pointer;
    width: 15vw;            
    height: 3vw;
    background-image: url('../../../public/images/Frame.png');
    background-position: center;
background-repeat: no-repeat;
background-size: contain;
}
.wrapper-right{
    cursor: pointer;
    display: flex;
    flex-direction: row;
    gap:4vw;
    margin-right: 4vw;
    margin-top: 5px;
    font-family: Inter;
font-size: 1.5vw;
font-weight: 600;
line-height: 22px;
letter-spacing: 0em;
text-align: left;
color: rgba(0, 0, 0, 1);

}